@import '../../../styles/utils/vars';
@import '../../../styles/utils/mixins';
@import '../../../styles/heading-styles';

.productpage {
  padding-block: calc(24px + $nav-height-mobile) 64px;
  height: 100%;

  @include on-desktop {
    padding-block: calc(24px + $nav-height-desktop) 80px;
  }

  &__top {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-top: 24px;

    @include on-tablet {
      margin-top: 40px;
    }
  }

  &__center {
    display: flex;
    gap: 16px;
    margin-block: 32px 24px;

    @include on-tablet {
      margin-top: 40px;
    }
  }

  &__select {
    display: flex;
    flex-direction: column;
    gap: 4px;

    &--text {
      color: $nav-link-color;
    }

    &--dropdown {
      border-radius: 8px;
      height: 40px;
      border: 1px solid $select-border-color;
      padding-inline: 12px 28px;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      background-image: url('./../../../../src/images/PhonePage/arrow-down.svg');
      background-repeat: no-repeat;
      background-origin: border-box;
      background-position: calc(100% - 12px);
      cursor: pointer;

      &:hover {
        border-color: $nav-link-color;
      }

      &:focus {
        outline: none;
        border-color: $text-color;
        background-image: url('./../../../../src/images/Footer/arrow.svg');
      }
    }

    &--dropdown-left {
      @include on-tablet {
        width: 187px;
      }

      @include on-desktop {
        width: 176px;
      }
    }

    &--dropdown-right {
      width: 125px;

      @include on-tablet {
        width: 136px;
      }

      @include on-desktop {
        width: 128px;
      }
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    place-items: center;
    gap: 40px 16px;
    padding: 0;
    margin: 0;
    list-style-type: none;

    @include on-tablet {
      grid-template-columns: repeat(2, 1fr);
      place-items: center stretch;
    }

    @include on-big-tablet {
      grid-template-columns: repeat(3, 1fr);
    }

    @include on-desktop {
      max-width: 1136px;
      margin-inline: auto;
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__pagination {
    display: flex;
    gap: 16px;
    align-items: center;
    margin-top: 24px;
    justify-content: center;

    @include on-tablet {
      margin-top: 40px;
    }
  }

  &__pagination-pageBlock {
    display: flex;
    gap: 8px;
    overflow: hidden;
    width: 192px;
    margin: 0;
    padding: 0;

    &--fix {
      width: auto;
    }
  }
}
