.icon {
  background-repeat: no-repeat;
  background-position: center;
  width: 16px;
  height: 16px;

  &__logo {
    background-image: url(../../images/Header/logo.svg);
    background-size: contain;
    width: 64px;
    height: 22px;
    margin: 13px 16px;

    &--nav {
      margin: 13px 16px;

      @include on-desktop {
        width: 80px;
        height: 28px;
        margin: 18px 24px;
      }
    }

    &--nav-menu {
      width: 64px;
      height: 22px;
      margin: 13px 16px;

      @include on-desktop {
        width: 80px;
        height: 28px;
        margin: 18px 24px;
      }
    }

    &--footer {
      width: 89px;
      height: 32px;
      margin: 0;
    }
  }

  &__menu,
  &__close {
    box-shadow: -1px 0 0 0 $shadow-color;
    padding: 16px;
    cursor: pointer;
  }

  &__menu {
    background-image: url(../../images/Header/manu.svg);
  }

  &__close {
    background-image: url(../../images/Header/closeImg.svg);
  }

  &__favourite-cart-Block {
    position: relative;
    padding: 16px;
    width: 16px;
    height: 16px;
    box-shadow: -1px 0 0 0 $shadow-color;

    &:hover {
      .icon__favourite,
      .icon__cart {
        transform: scale(1.3);
      }
    }

    &--menu {
      width: 50%;
      height: 64px;
      border-top: 1px solid $shadow-color;
      padding: 0;
      display: grid;
      place-items: center;

      @include on-tablet {
        display: block;
        padding: 16px;
        width: 16px;
        height: 16px;
      }
    }

    &--active::after {
      position: absolute;
      width: 100%;
      height: 3px;
      content: "";
      background: $text-color;
      bottom: 0;
      left: 0;
      right: 0;
    }

    @include on-desktop {
      padding: 24px;
    }
  }

  &__elipse {
    position: absolute;
    box-sizing: border-box;
    transform: translate(35%, -135%);
    display: grid;
    place-items: center;
    border: 1px solid $bg-button-color;
    border-radius: 50%;
    width: 22px;
    height: 22px;
    background: $elipse-color;
    font-family: Mont-SemiBold, sans-serif;
    font-size: 12px;
    color: $bg-button-color;

    &--menu {
      transform: translate(48%, -50%);

      @include on-tablet {
        transform: translate(35%, -135%);
      }
    }
  }

  &__favourite,
  &__cart {
    @include hover(transform, scale(1.3));
  }

  &__favourite {
    background-image: url(../../images/Header/favourite.svg);
  }

  &__cart {
    background-image: url(../../images/Header/card.svg);
  }

  @include on-tablet {
    &__close,
    &__menu {
      display: none;
    }
  }
}
