@import '../../../styles/utils/vars';
@import '../../../styles/utils/mixins';

.cart {
  padding-block: calc(24px + $nav-height-mobile) 56px;
  height: 100%;

  @include on-tablet {
    padding-block: calc(40px + $nav-height-desktop) 64px;
  }

  @include on-tablet {
    padding-bottom: 80px;
  }

  &__grid {
    @include on-desktop {
      @include page-grid;
    }
  }

  &__priceContainer {
    display: grid;
    place-items: center;
    grid-column: 17 / -1;

    @include on-desktop {
      align-items: start;
      margin-top: 32px;
    }
  }

  &__list {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 0;
    margin: 0;
    gap: 16px;
    margin-top: 32px;
    grid-column: 1 / 17;
  }

  &__item {
    display: flex;
    flex-direction: column;
    border: 1px solid $shadow-color;
    border-radius: 16px;
    gap: 16px;
    width: 100%;
    max-width: 330px;
    padding: 16px;
    box-sizing: border-box;

    &:hover .cart__item-imageBlock--photo {
      transform: scale(1.3);
    }

    @include on-tablet {
      flex-direction: row;
      max-width: 690px;
      padding: 24px;
      gap: 0;
      justify-content: space-between;
    }

    @include on-desktop {
      max-width: 100%;
    }
  }

  &__item-imageBlock,
  &__item-priceBlock {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__item-imageBlock {
    @include on-tablet {
      gap: 24px;
    }

    &--close,
    &--photo {
      object-fit: contain;
      @include hover(transform, scale(1.3));
    }

    &--close {
      height: 16px;
      cursor: pointer;
    }

    &--photo {
      padding-block: 6px;
      width: 66px;
      height: 66px;
    }

    &--name {
      min-width: 130px;
      width: min-content;

      @include on-tablet {
        max-width: 160px;
        width: max-content;
      }

      @include on-desktop {
        max-width: 100%;
      }
    }
  }

  &__item-priceBlock {
    @include on-tablet {
      width: 200px;
    }

    &--quantityBlock {
      display: flex;
      align-items: center;
    }


    &--quantity {
      width: 20px;
      text-align: center;
      margin-inline: 8px;
    }

    &--price {
      font-family: Mont-Bold, sans-serif;
    }
  }

  &__totalPrice {
    box-sizing: border-box;
    border: 1px solid $shadow-color;
    border-radius: 16px;
    padding: 24px;
    max-width: 330px;
    width: 100%;
    margin-top: 32px;

    @include on-tablet {
      max-width: 690px;
    }

    @include on-desktop {
      margin: 0;
    }

    &--priceInfo {
      display: grid;
      place-items: center;
      border-bottom: 1px solid $shadow-color;
      box-sizing: border-box;
      padding-bottom: 16px;
      margin-bottom: 16px;

      @include on-desktop {
        padding-bottom: 25px;
        margin-bottom: 25px;
      }
    }

    &--priceInfo-price {
      font-size: 32px;
    }
  }
}
