@import '../../../styles/utils/vars';
@import '../../../styles/utils/mixins';

.notfound {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 25px;
  padding-block: calc(24px + $nav-height-mobile) 32px;

  @include on-tablet {
    gap: 50px;
    padding-block: calc(24px + $nav-height-desktop) 0;
  }

  &__titleBlock {
    display: flex;
    align-items: center;
  }

  &__image {
    width: 100%;
    height: 300px;
    object-fit: contain;

    @include on-tablet {
      height: 550px;
    }
  }
}
