.link {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Mont-Bold, sans-serif;
  color: $nav-link-color;
  text-decoration: none;
  font-size: 12px;
  box-sizing: border-box;
  text-transform: uppercase;
  padding-block: 8px;
  position: relative;

  &__footer {
    cursor: pointer;
    justify-content: start;
    font-family: Mont-SemiBold, sans-serif;
    padding: 0;
    line-height: 11px;

    &--back {
      text-transform: none;
      padding: 0;
    }
  }

  @include on-tablet {
    height: 100%;
  }

  &__active::after {
    position: absolute;
    height: 2px;
    bottom: 0;
    left: 0;
    right: 0;
    content: "";
    background: $text-color;

    @include on-tablet {
      height: 3px;
    }
  }

  &:hover {
    color: $text-color;
  }
}
